* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.search-container {
  height: 90px;
  padding-top: 25px;
  background-color: #188ce8;
  justify-content: center;
  display: flex;
  width: 100vw;
  position: fixed;
}

.search-form {
  display: flex;
  flex-wrap: wrap;
  height: 40px;
  justify-content: center;
}

.search-form:hover .search-box:focus {
  border: 3px solid #188ce8;
}

.search-input {
  width: 400px;
  border: 1px solid #d9d9d9;
  padding-left: 40px;
  font-size: 18px;
  justify-content: center;
  padding-left: 10px;
}

.search-input::placeholder {
  font-size: 16px;
  color: #7575759c;
}

.search-btn {
  width: 40px;
  cursor: pointer;
  background-image: url(../images/magnifying-glass.svg);
  background-repeat: no-repeat;
  background-size: 15px;
  background-position: center;
  border: none;
}

.loadMore-btn {
  margin: 0 auto;
  margin-top: 50px;
  display: block;
}

button:disabled {
  background-color: #c1c1c1;
  cursor: not-allowed;
}

.hidden {
  display: none;
}

.gallery {
  margin-top: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
}
